:root {
  --color-y: #ffca3a;
  --color-b: #1982c4;
  --color-g: #8ac926;
  --color-r: #ff595e;

  --turn-height: 500px;
}

.heroesRecords {
  display: flex;
}

.heroRecords > h3 {
  font-size: 14px;
  margin: 0 0 10px;
}

.heroRecords {
  margin: 0 5px;
  flex: 1;
}

.heroesRecords.final {
  padding: 10px 0;
  background: #fafafa;
  font-size: 11px;
}

.heroRecords .turn {
  min-height: var(--turn-height);
  box-sizing: border-box;
}

.heroRecords .doubleTurn .turn {
  min-height: calc(var(--turn-height)/2 - 5px);
}

.turn {
  margin: 0 0 10px;
  padding: 5px 10px 10px;
  outline: #ccc solid 1px;
  outline-offset: -1px;
}

.turn.isSuccess {
  outline: var(--color-g) solid 1px;
  outline-offset: -1px;
}
.turn.isFailure {
  outline: var(--color-r) solid 1px;
  outline-offset: -1px;
}

.turn .treasure {
  display: flex;
  min-height: calc(var(--turn-height) - 15px);
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.doubleTurn .turn .treasure {
  min-height: calc(var(--turn-height)/2 - 20px);
}

.turn .treasure .shiftSpell {
    height: 100px;
    width: 70px;
    background: cornsilk;
    box-shadow: 2px 2px 2px rgb(0 0 0 / 30%);
    text-align: center;
    font-size: 11px;
    line-height: 1.2;
    padding: 35px 0;
    box-sizing: border-box;
    font-style: italic;
}

.turn .treasure .healPotion {
  height: 120px;
  width: 60px;
  background: #f689c3;
  box-shadow: 0px 10px 10px -3px rgb(0 0 0 / 30%);
  text-align: center;
  font-size: 11px;
  line-height: 1.2;
  padding: 45px 0;
  border-radius: 25px;
  box-sizing: border-box;
  font-style: italic;
}

.turn .treasure .rune {
  height: 80px;
  width: 60px;
  background: #b5bdc6;
  box-shadow: 3px 3px 3px 3px rgb(0 0 0 / 30%);
  text-align: center;
  font-size: 11px;
  line-height: 1.2;
  padding: 30px 0;
  border-radius: 15px;
  box-sizing: border-box;
  font-style: italic;
}



.turn h2 {
  font-size: 14px;
  margin: 0 0 10px;
} 

.turnDescription {
  display: flex;
  font-size: 10px;
  align-items: center;
  margin-bottom: 10px;
}

.turnContent {
  display: flex;
}

.turnPhase {
  margin: 0 5px 0 0;
}

.hero {
  flex: 1 0 120px;
  margin-right: 10px;
}

.hero h3 {
  font-size: 12px;
  margin: 0;
}

.hero .monsterTrophies {
  font-size: 14px;
  margin-top: 5px;
}

.turn .hero .monsterTrophies {
  display: none;
}

.meetDetails {
  font-size: 12px;
  margin-left: 20px;
}

.spells {
  display: flex;
}

.spells .spell {
  display: block;
  background-color: grey;
  width: 10px;
  height: 10px;
  font-size: 0;
  margin: 1px;
}

.spells .spell.spell-1 { background-color: var(--color-y); }
.spells .spell.spell-2 { background-color: var(--color-b); }
.spells .spell.spell-3 { background-color: var(--color-g); }
.spells .spell.spell-4 { background-color: var(--color-r); }

.biome-matrix {
  display: flex;
}

.biome-matrix .spells-matrix {
  border: #ccc solid 1px;
}

.monster-matrix { 
  display: flex;
}

.monster-matrix .spells-matrix {
  border: #ccc solid 1px;
}

.monster-matrix .spells .spell.spell-0 { background-color: #fff; opacity: .9;}
.monster-matrix .spells .spell.spell-1 { background-color: transparent; }

.monsterAndBiomeMatrixes {
  position: relative;
  margin-bottom: 10px;
}

.monsterAndBiomeMatrixes .monster-matrix {
  position: absolute;
  top: 0;
  left: 0;
}

.failureRecord .label {
  font-size: 10px;
  margin-right: 5px;
}

.failureRecord .spells {
  display: flex;
}
.failureRecord .spells .spell {
  font-size: 8px;
  line-height: 10px;
  font-weight: bold;
  width: 10px;
  text-align: center;
}

.failureRecord .spells .spell.empty {
  opacity: .3;
}

.shiftSpellUsed,
.runeUsed {
  margin: 5px 5px 5px 0;
  display: flex;
}

.shiftSpellUsed .label,
.runeUsed .label {
  font-size: 10px;
  margin-right: 5px;
}
